.nav {
  position: relative;
  display: flex;
  min-height: 56px;
}
.nav__list {
  display: flex;
}
@media (max-width: 767px) {
  .nav {
    flex-direction: column;
  }
  .nav__toggle {
    position: absolute;
    top: 17px;
    right: 20px;
    z-index: 5;
    width: 23px;
    height: 21px;
    background: transparent;
  }
  .nav__list {
    flex-direction: column;
    margin-top: 0;
    transition: margin 0.4s ease-out;
  }
  .nav__item a {
    display: block;
    padding: 8px 0 7px 0;
    border-bottom: 1px solid #9ac2db;
    background: #81b3d2;
    box-shadow: inset 0 -1px 0 0 #678fa8;
    color: #fff;
    font-size: 18px;
    line-height: 40px;
    font-weight: 700;
    text-align: center;
    transition: background 0.3s linear, color 0.3s linear;
  }
  .nav__item a[href]:hover {
    background: #669ec0;
  }
  .nav__item a[href]:active {
    background: #5496bd;
    color: rgba(255, 255, 255, 0.3);
  }
  .nav__item:last-of-type a {
    border: none;
    box-shadow: inset 0 -7px 7px -7px rgba(0, 0, 0, 0.35);
  }
  .nav--closed .nav__toggle::after {
    content: "";
    position: absolute;
    display: block;
  }
  .nav--closed .nav__toggle::before {
    content: "";
    position: absolute;
    display: block;
    top: 0;
    right: 0;
    width: 100%;
    height: 4px;
    background: #81b3d2;
    box-shadow: 0 8px 0 0 #81b3d2, 0 16px 0 0 #81b3d2;
    transition: background 0.3s linear, box-shadow 0.3s linear;
  }
  .nav--closed .nav__toggle:hover::before {
    background: #669ec0;
    box-shadow: 0 8px 0 0 #669ec0, 0 16px 0 0 #669ec0;
  }
  .nav--closed .nav__toggle:active::before {
    background: #5496bd;
    box-shadow: 0 8px 0 0 #5496bd, 0 16px 0 0 #5496bd;
  }
  .nav--opened .nav__toggle::before,
  .nav--opened .nav__toggle::after {
    content: "";
    position: absolute;
    display: block;
    top: -3px;
    right: 10px;
    width: 4px;
    height: 28px;
    background: #fff;
    transition: background 0.3s linear, transform 0.3s linear;
  }
  .nav--opened .nav__toggle::before {
    transform: rotate(45deg);
  }
  .nav--opened .nav__toggle::after {
    transform: rotate(-45deg);
  }
  .nav--opened .nav__toggle:hover::before,
  .nav--opened .nav__toggle:hover::after {
    background: #f2f2f2;
  }
  .nav--closed .nav__list {
    margin-top: -223px;
  }
  .nav--nojs .nav__toggle {
    display: none;
  }
  .nav--nojs .nav__list {
    margin-top: 0;
  }
}
@media (min-width: 768px) {
  .nav__toggle {
    display: none;
  }
  .nav__list {
    width: 100%;
  }
  .nav__item {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20%;
    height: 56px;
  }
  .nav__item:nth-of-type(2) {
    margin-right: 10%;
  }
  .nav__item:nth-of-type(3) {
    margin-left: 10%;
  }
  .nav__item a {
    font-size: 14px;
    line-height: 26px;
    font-weight: 700;
    transition: color 0.3s linear;
  }
  .nav__item a[href]:hover {
    color: #81b3d2;
  }
  .nav__item a[href]:active {
    color: rgba(0, 0, 0, 0.3);
  }
  .nav__item--active a {
    color: #766357;
  }
  .nav__item--active a:hover {
    color: #604e43;
  }
  .nav__item--active a:active {
    color: rgba(118, 99, 87, 0.3);
  }
}
