html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  min-width: 320px;
  background: @gray-light;
  color: @primaryColor;
  .font(full);
  text-transform: uppercase;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

sup,
sub {
  position: relative;
  vertical-align: baseline;
  .font(shortest; 50%; 0);
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.icon-svg {
  .def-ib(middle);
  max-width: 100%;
  overflow: hidden;
}
